@font-face {
  font-family: 'awesome';
  src:  url('awesome/awesome.eot?txyjyv');
  src:  url('awesome/awesome.eot?txyjyv#iefix') format('embedded-opentype'),
    url('awesome/awesome.ttf?txyjyv') format('truetype'),
    url('awesome/awesome.woff?txyjyv') format('woff'),
    url('awesome/awesome.svg?txyjyv#awesome') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="fa-"], [class*=" fa-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'awesome' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  text-rendering: auto;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.fa-check:before {
  content: "\f00c";
}
.fa-trash-o:before {
  content: "\f014";
}
.fa-pencil:before {
  content: "\f040";
}
.fa-play:before {
  content: "\f04b";
}
.fa-check-circle:before {
  content: "\f058";
}
.fa-spinner:before {
  content: "\f110";
}
.fa-trash:before {
  content: "\f1f8";
}
