*,
*:before,
*:after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	color: #333;
	font-family: Helvetica, sans-serif;
	background: #f1f1f1;
}

.bg-white {
	background-color: white;
}

.rounded {
	border-radius: .25rem;
}

.shadow-md {
	box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}

.rounded-lg {
	border-radius: .5rem;
}

.md-shadow {
	box-shadow: 3px;
}

.italic {
	font-style: italic;
}

.bold {
	font-weight: bold;
}

.cursor-pointer {
	cursor: pointer;
}

.break-line {
	line-break: anywhere;
}

ul {
	list-style-type: none;
	padding: 0;
}

.icon-button {
	cursor: pointer;
	border: none;
	padding: 0.25rem;
	background: none;
}

.input {
	border: 2px solid;
	border-radius: 4px;
	font-size: 1rem;
	margin: 0.25rem;
	padding: 0.5rem;
}

.textarea-horizontal {
	resize: horizontal;
}

.list-element {
	background-color: white;
}

.hidden {
	visibility: hidden;
}
